import { mapGetters } from 'vuex';
import AlertCircle from 'vue-material-design-icons/AlertCircle';
import FolderMultipleImage from 'vue-material-design-icons/FolderMultipleImage';
import { NcEmptyContent, isMobile } from '@nextcloud/vue';
import FilesSelectionMixin from '../../mixins/FilesSelectionMixin.js';
import FilesListViewer from '.././FilesListViewer.vue';
import File from '.././File.vue';
import FolderIllustration from '../../assets/Illustrations/folder.svg';
import SemaphoreWithPriority from '../../utils/semaphoreWithPriority.js';
export default {
  name: 'CollectionContent',
  components: {
    AlertCircle,
    FolderMultipleImage,
    NcEmptyContent,
    FilesListViewer,
    File
  },
  mixins: [FilesSelectionMixin, isMobile],
  props: {
    collection: {
      type: Object,
      default: () => undefined
    },
    collectionFileIds: {
      type: Array,
      required: true
    },
    loading: {
      type: Boolean,
      default: false
    },
    error: {
      type: [Error],
      default: ''
    },
    semaphore: {
      type: SemaphoreWithPriority,
      required: true
    }
  },

  data() {
    return {
      FolderIllustration,
      appContent: document.getElementById('app-content-vue')
    };
  },

  computed: { ...mapGetters(['files'])
  },
  methods: {
    openViewer(fileId) {
      const file = this.files[fileId];
      OCA.Viewer.open({
        fileInfo: file,
        list: this.collectionFileIds.map(fileId => this.files[fileId]).filter(file => !file.sectionHeader),
        loadMore: file.loadMore ? async () => await file.loadMore(true) : () => [],
        canLoop: file.canLoop
      });
    }

  }
};